import React from "react";
import styled from 'styled-components';
import background from "../images/about/round_background.png";
import logo from "../images/logo/logo.svg"
import solux from "../images/about/solux_img.png";
import dacos from "../images/about/dacos_img.png";
import poster1 from "../images/about/poster1.svg";
import poster2 from "../images/about/poster2.svg";
import { useMediaQuery } from 'react-responsive';
import Slider from "react-slick";
import "../style/slick-theme.css";
import "../style/slick.css";

const AboutContainer = styled.div`
    width: 100%;
    height: auto;
`

const LogoContainer = styled.div`
    height: 97vh;
`

const About = styled.div`
    padding: 0vw 15vw;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const MainConatiner = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 80px;
    padding-bottom: 30px;
`

const RoundBackground = styled.div`
    background-image: url(${background});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    position: relative;
    width: 100%;
    height: clamp(80%, 1.5vw, 150%);
    border: none;
`

const DXSLogo = styled.div`
    background-image: url(${logo});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: clamp(330px, 50vw, 370px);
    height: clamp(80px, 50vw, 120px);
`

const MainBoldText = styled.div`
    font-weight: 800;
    font-size: clamp(2.5rem, 60vw, 2.7rem);
`

const MainLightText = styled.div`
    font-weight: 100;
    font-size: 0.65rem;
`

const InfoContainer = styled.div`
    margin-top: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
`

const InfoTextContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
`

const InfoBoldText = styled.div`
    font-weight: 600;
    font-size: clamp(1.3rem, 1.5vw, 1.7rem);
    margin-top: 20px;
`

const InfoLightText = styled.div`
    width: 55%;
    font-weight: 200;
    font-size: clamp(0.7rem, 1.5vw, 0.8rem);
    line-height: 160%;
`

const DSTitle = styled.div`
    font-weight: 600;
    font-size: clamp(2.2rem, 1vw, 2.5rem);
    color: #0057FF;
`

const DSSubtitle = styled.div`
    font-weight: 100;
    font-size: 0.7rem;
    margin-bottom: 10px;
`

const DSContent = styled.div`
    font-weight: 200;
    width: 100%;
    text-align : center;
    word-break: keep-all;
    font-size: clamp(0.7rem, 100vw, 0.8rem);
    line-height: 160%;
    margin-top: 20px;
`

const DacosInfoContainer = styled.div`
    height: auto;
    margin: 200px 0px;
`

const SoluxInfoContainer = styled.div`
    margin-bottom: 40px;
`

const PosterContainer = styled.div`
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
`

const PosterWrapper = styled.div`
    width: 40%;
    max-height: 83vh;
    border: 1px solid #FFFFFF;
    border-radius: 3%;
    overflow: hidden;
`

const PosterImage = styled.img`
    width: 100%;
    height: auto;
    display: block;
    object-fit: contain;
`

const MobilePosterContainer = styled.div`
    margin-top: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
`

const MobilePoster1 = styled.div`
    width: 80%;
    height: 100vw;
    background-image: url(${poster1});
    background-size: cover;
    background-repeat: no-repeat;
`

const MobilePoster2 = styled.div`
    width: 80%;
    height: 100vw;
    background-image: url(${poster2});
    background-size: contain;
    background-repeat: no-repeat;
`

const DacosImage = styled.div`
    width: 80%;
    height: clamp(15vw, 100vw, 25vw);
    background-image: url(${dacos});
    background-size: contain;
    background-repeat: no-repeat;
`;

const SoluxImage = styled.div`
    width: 80%;
    height: clamp(15vw, 100vw, 25vw);
    background-image: url(${solux});
    background-size: contain;
    background-repeat: no-repeat;
`;

const MobileImageContainer = styled.div`
    display: flex;
    width: 70vw;
    height: 100%;
`

const AboutPage = () => {
    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
      };

    return (
        <AboutContainer>
            <LogoContainer>
                <MainConatiner>
                    <DXSLogo />
                    <MainBoldText></MainBoldText>
                    <MainLightText>SOOKMYUNG WOMEN'S UNIVERSITY</MainLightText>
                </MainConatiner>

                <RoundBackground/> 
            </LogoContainer>

            <About>
                { isMobile && <>
                <MobilePosterContainer>
                    <MobileImageContainer>
                        <Slider {...settings}>
                            <MobilePoster1 />
                            <MobilePoster2 />
                        </Slider>
                    </MobileImageContainer>
                    <InfoTextContainer>
                        <InfoBoldText>DACOS X SOLUX </InfoBoldText>
                        <DSContent>DACOS X SOLUX              ,         .</DSContent>
                    </InfoTextContainer>
                </MobilePosterContainer>
                    
                </>
                }
                
                { !isMobile && <>
                    <InfoContainer>
                    <PosterContainer>
                        <PosterWrapper>
                            <PosterImage src={poster1} alt="Poster 1" />
                        </PosterWrapper>
                        <PosterWrapper>
                            <PosterImage src={poster2} alt="Poster 2" />
                        </PosterWrapper>
                    </PosterContainer>
                        <InfoTextContainer>
                            <InfoBoldText>DACOS X SOLUX </InfoBoldText>
                            <InfoLightText>DACOS X SOLUX              ,         .</InfoLightText>
                        </InfoTextContainer>
                    </InfoContainer>
                </>}
                <DacosInfoContainer>
                    <InfoTextContainer>
                        <DacosImage />
                        <DSTitle>DACOS</DSTitle>
                        <DSSubtitle>  </DSSubtitle>
                        <DSContent style={{ width: !isMobile? "65%" : "100%"}}>DACOS      , "Data Analysis Club of Sookmyung"  . DACOS  , ,     ,          .</DSContent>
                    </InfoTextContainer>
                </DacosInfoContainer>

                <SoluxInfoContainer>
                    <InfoTextContainer>
                        <SoluxImage />
                        <DSTitle>SOLUX</DSTitle>
                        <DSSubtitle>  </DSSubtitle>
                        <DSContent style={{ width: !isMobile? "65%" : "100%"}}>   Sookmyung Linux, SOLUX    . SOLUX    ,       IT   IT   .</DSContent>
                    </InfoTextContainer>
                </SoluxInfoContainer>
            </About>
        </AboutContainer>
        
    );
};

export default AboutPage;
