import { React, useState } from "react";
import styled from 'styled-components';
import background from "../images/timeline/process_background.png";
import Button from "../component/Button";
import none from "../images/none.png";
import { useMediaQuery } from 'react-responsive';

const TimelineContainer = styled.div`
    width: 100%;
    background-color: transparent;
`

const InfoContainer = styled.div`
    height: 90vh;
    width: 100%;
    margin-top: 1vh;
    margin-bottom: 6vh;
    background-image: url(${background});
    background-size: cover;
    background-repeat: repeat;
    background-position: top;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
`

const TitleContainer = styled.div`
    display: flex;
    align-items: baseline;
    background-color: transparent;
    width: clamp(60vw, 1.5vw, 70vw);
`

const TitleText = styled.div`
    font-size: clamp(1.5rem, 1vw, 1.8rem);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.6rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    margin-left: 5px;
`

const GraySubtitleText = styled.div`
    font-weight: 400;
    font-size: clamp(0.7rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    width: clamp(60vw, 1.5vw, 70vw);
    margin-top: 20px;
    line-height: 140%;
    letter-spacing: 1px;
`

const SubtitleText = styled.div`
    font-size: clamp(0.8rem, 1vw, 0.9rem);
    font-weight: 200;
    color: #B2B2B2;
    background-color: transparent;
    width: clamp(60vw, 1.5vw, 70vw);
`

const PartContainer = styled.div`
    background-color: transparent;
    margin: 40px 0px 0px 0px;
    width: clamp(60vw, 1.5vw, 70vw);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`

const Part = styled.div`
    background-color: transparent;
    display: flex;
    width: 80%;
`

const BlueText = styled.div`
    color: #0057FF;
    width: 7.5rem;
    font-size: 1rem;
    font-weight: 400;
    background-color: transparent;
`

const TextButton = styled.div`
    width: 80%;
    height: 30px;
    font-size: clamp(1.1rem, 1.5vw, 1.3rem);
    font-weight: 600;
    background-color: transparent;
    cursor: pointer;
    padding-right: 0.5rem;
`

const WhiteText = styled.div`
    width: clamp(60vw, 1.5vw, 70vw);
    font-size: 1rem;
    font-weight: 400;
    background-color: transparent;
`

const ContentText = styled.div`
    width: clamp(60vw, 1.5vw, 70vw);
    margin-top: 30px;
    font-size: clamp(0.9rem, 1vw, 1rem);
    font-weight: 400;
    background-color: transparent;
    line-height: 130%;
    letter-spacing: 0.3px;
`

const TimetableIamge = styled.div`
    width: clamp(30vw, 1.5vw, 35vw);
    height: 250px;
    background-image: url(${none});
    background-size: cover;
    background-repeat: no-repeat;
`

const TimetableContainer = styled.div`
    display: flex;
    width: clamp(60vw, 1.5vw, 70vw);
`

const TimelinePage = () => {
    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [isDS, setIsDS] = useState(true);

  const handleClick = (part) => {
    if (part === 'DS' && !isDS) {
      setIsDS(true);
    } else if (part === 'Dev' && isDS) {
      setIsDS(false);
    }
  };

    return (
        <TimelineContainer>
            <InfoContainer>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>APPLY</GrayTitleText>
                </TitleContainer>
                <SubtitleText>24. 09. 08 (SUN) 10:00 -24. 09. 14 (SAT) 23:59</SubtitleText>

                { isMobile &&
                    <>
                        <PartContainer>
                            <Part>
                                <TextButton
                                    onClick={() => handleClick('DS')}
                                    style={{
                                    color: isDS ? "#0057FF" : "#767676",
                                    borderBottom: isDS ? "2px solid #FFFFFF" : "2px solid #767676"
                                    }}
                                    disabled={isDS}
                                >
                                     
                                </TextButton>
                                <TextButton
                                    onClick={() => handleClick('Dev')}
                                    style={{
                                    color: !isDS ? "#0057FF" : "#767676",
                                    borderBottom: !isDS ? "2px solid #FFFFFF" : "2px solid #767676"
                                    }}
                                    disabled={!isDS}
                                >
                                    
                                </TextButton>
                            </Part>

                            { isDS && <>
                                <WhiteText style={{ marginTop: "10px"}}>DACOS 2   OB</WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>
                                    * 5,000  ,    .<br />
                                    *   ,       .<br />
                                    *          .
                                </GraySubtitleText>
                                <Button
                                    onClick={() => {
                                        window.open("https://forms.gle/6KaV4nXxNoFtnS6p7") //   
                                    }}
                                    style = {{ height: "40px", width: "100px"}}
                                    >
                                    
                                </Button>
                            </>
                            }

                            { !isDS && <>
                                <WhiteText style={{ marginTop: "10px"}}>SOLUX 29   OB     </WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>
                                    * 5,000  ,    .<br />
                                    *   ,       .<br />
                                    *          .
                                </GraySubtitleText>

                                <Button
                                    onClick={() => {
                                        window.open("https://forms.gle/1Bt1vyWGVtyYf2SH9") //   
                                    }}
                                    style = {{ height: "40px", width: "100px"}}
                                    >
                                    
                                </Button>
                            </>
                            }
                        </PartContainer>
                    </>
                }
                
                { !isMobile && 
                    <>
                    <PartContainer>
                        <Part>
                            <BlueText> </BlueText>   
                            <WhiteText>DACOS 2   OB</WhiteText>
                        </Part>
                        
                        <Part>
                            <BlueText></BlueText>
                            <WhiteText>SOLUX 29   OB     </WhiteText>
                        </Part>
                    </PartContainer>
                    <GraySubtitleText style={{ marginBottom: "50px"}}>
                        * 5,000  ,    .<br />
                        *   ,       .<br />
                        *          .
                    </GraySubtitleText>

                    <Button
                        onClick={() => {
                            window.open("https://linktr.ee/DxSHackathon")
                        }}
                        style = {{ height: "40px", width: "100px"}}
                        >
                        
                    </Button>
                    </>
                }
                
            </InfoContainer>
            
            <InfoContainer>
                <TitleContainer>
                    <TitleText> </TitleText>
                    <GrayTitleText>TEAM REVEAL</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 10. 31 (THU)</SubtitleText>
                
                <ContentText>  2,  2,  2   . (   ) <br />
                      .</ContentText>

                <GraySubtitleText>*   </GraySubtitleText>

            </InfoContainer>

            <InfoContainer>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>PRE - PREPARATION</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 11. 01 (FRI) - 24. 11. 07 (THU)</SubtitleText>
                
                <ContentText>            . -      . (  )
                    <br />     ,     .</ContentText>

            </InfoContainer>

            <InfoContainer>
                <TitleContainer>
                    <TitleText>  </TitleText>
                    <GrayTitleText>HACKATHON</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 11. 08 (FRI) 10:00 - 24. 11. 09 (SAT) 14:00</SubtitleText>
                
                <ContentText>
                     2  <br />
                          .<br />
                </ContentText>
                <ContentText style={{ marginTop: "10px"}}>Time table</ContentText>
                <TimetableContainer>
                    <TimetableIamge />
                    <TimetableIamge />
                </TimetableContainer>

                <GraySubtitleText style={{ marginTop: "5px"}}>*, ,      </GraySubtitleText>
            </InfoContainer>
        </TimelineContainer>
    );
};

export default TimelinePage;
